* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: sans-serif;
}

.container {
	position: relative;
	width: 100%;
	height: 500vh;
	overflow: hidden;
}

.start {
	position: absolute;
	top: 0%;
	background: rgb(21, 46, 38);
	width: 100%;
	height: 100vh;
	z-index: 1;
}

a {
	text-decoration: none;
}
